.article {
   --padding-panel: 4.5rem;
   --gap-flow: 4.5rem;

   & h1 {
      font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
      font-size: 4rem;
      position: relative;

      &::first-letter {
         color: var(--color-secondary);
         font-weight: 800;
         text-transform: uppercase;
      }

      &::before {
         position: absolute;
         content: '';
         width: 10ch;
         height: 5px;
         background: linear-gradient(
            90deg,
            var(--color-background),
            var(--color-primary),
            var(--color-secondary),
            var(--color-background)
         );
         bottom: 0;
      }
   }

   &__grid {
      display: flex;
      flex-wrap: wrap;
      gap: 2.5rem;
   }

   &__card {
      flex: 1 0 20rem;
      overflow: hidden;

      display: grid;
      grid-template-rows: 20rem max-content;
      grid-template-columns: 1fr;
      text-decoration: none;

      &:hover img {
         filter: grayscale(0%);
         transform: scale(1.02);
      }

      & img {
         width: 100%;
         height: 100%;
         object-fit: cover;
         transition: 200ms ease-in-out;
         z-index: -1;
         filter: grayscale(90%);
      }

      & div {
         padding: 1rem;
         color: var(--color-background);
         background: var(--color-text);
         position: relative;

         & small {
            color: var(--color-secondary);
         }

         &::before {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 5px;
            background: linear-gradient(
               90deg,
               var(--color-primary),
               var(--color-secondary)
            );
         }
      }
   }
}
